.header  {
    width: 100%;
    background-color: #000000;
    margin-bottom: 10px;
}




.logo_title {
    font-size: 50px;
    color: white;
    font-weight: 700;
}

.login_block {
    color: white;
    font-size: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.login_block a {
    color: white;
}

.avatar {
    width: 70px;
    height: 70px;
    margin-left: 20px;
}

.avatar img {
    width: 70px;
    height: 70px;
    border-radius: 50px;
}