.post_block {
    margin-top: 5px;
    padding: 10px;
    width: 60%;
    margin-left: 39%;
}

.post_item_area {
    display: flex;
    flex-direction: column;
    align-items:flex-end;
    min-width: 500px;


}

textarea {
    min-width: 500px;
    resize: none;
    min-height: 70px;
}

.post_item_button {
    color: white;
    background-color: #00BFFF;
    margin-top: 10px;
    width: 100px;
    height: 40px;
    border: none;
}

.post_block_title {
    font-size: 25px;

}

.post_block_item {
    background-color: white;
    padding: 10px;
    display: flex;
    flex-direction: column;
    align-items:flex-end;
}
